<template>
  <div class="alert" :class="'alert-' + type">
    <h2> {{ text1 }} <br> {{ text2 }} </h2>
    <hr>
    <button
      class="btn btn-primary"
      @click="$emit('onNext')">
        Continue
      </button>
  </div>
</template> 
 
<script>
  export default {
    props: ['type', 'text1', 'text2']
  }    
</script>

<style scoped lang="scss">
  .alert {
    text-align: center;
  }

  h2, .btn {
    margin: 20px 0;
  }
</style>


