<template>
  <div class="training">
    <h3 class="text-center">Math Training. Level {{ level + 1 }}</h3>
    <hr> 
    <div class="progress">
      <div class="progress-bar" :style="progressStyles"></div>
    </div>
    <div class="box">
      <transition name="left" mode="out-in">
        <app-start-screen 
          v-if="state == 'start'"
          @onStart="onStart()">
            
        </app-start-screen>

        <app-question 
          v-else-if="state == 'question'"
          @success="onQuestSuccess"
          @error="onQuestError"
          :settings="levels[level]">
            
        </app-question>

        <app-message 
          v-else-if="state == 'message'"
          :type="messages.type"
          :text1="messages.text1"
          :text2="messages.text2"
          @onNext="onNext">
          
        </app-message>

        <app-result-screen 
          v-else-if="state == 'result'"
          :stats="stats"
          @repeat="onStart"
          @nextLevel="onNextLevel">
            
        </app-result-screen>

        <div v-else>Unknown state</div>
      </transition>
    </div>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      state: 'start',
      stats: {
        success: 0,
        error: 0
      },
      messages: {
        type: '',
        text1: '',
        text2: ''
      },
      questMax: 3,
      level: 0,
      levels: [
        {
          from: 10,
          to: 40,
          range: 5,
          variants: 2
        },
        {
          from: 100,
          to: 200,
          range: 20,
          variants: 4
        },
        {
          from: 500,
          to: 900,
          range: 40,
          variants: 6
        }
      ]
    }
  },

  computed: {
    questDone() {
      return this.stats.success + this.stats.error;
    },
    progressStyles() {
      return {
        width: (this.questDone / this.questMax * 100) + '%'
      };
    }
  },

  methods: {
    onStart() {
      this.state = 'question';
      this.stats.success = 0;
      this.stats.error = 0;
    },
    onQuestSuccess() {
      this.state = 'message';
      this.messages.text1 = 'Good Job!';
      this.messages.text2 = '';
      this.messages.type = 'success';
      this.stats.success++;
    },
    onQuestError(msg1, msg2) {
      this.state = 'message';
      this.messages.text1 = msg1;
      this.messages.text2 = msg2;
      this.messages.type = 'warning';
      this.stats.error++;
    },
    onNext() {
      if (this.questDone < this.questMax) {
        this.state = 'question';
      }
      else {
        this.state = 'result';
      }
    },
    onNextLevel() {
      this.level++;
      this.onStart();
    }
  }
}
</script>

<style lang="scss" scoped>

  .training {
    max-width: 500px;
    margin: 20px auto;
  }

  .box {
    margin: 10px 0;
  }

  .progress-bar {
    transition: width .5s linear;
  }

  .left-enter {

  }

  .left-enter-active {
    animation: leftInX 0.2s linear;
  }

  .left-leave {

  }

  .left-leave-active {
    animation: leftOutX 0.2s linear; 
  }

  @keyframes leftInX {
    from{transform: translateX(20px);opacity:0}
    to{transform: translateX(0);opacity:1}
  }

  @keyframes leftOutX {
    from{transform: translateX(0);opacity:1}
    to{transform: translateX(-20px);opacity:0}
  }

</style>



